import React, { Component } from 'react'

class UserGreeting extends Component
{
    constructor()
    {
        super()
        this.state = {
            isLoggedin : true
        }
    }


    render()
    {
        if(this.state.isLoggedin)
        {
            return <h1>Im Abinav</h1>
        }else
        {
            return <h1>Im Guest</h1>
        }
    }

}

export default UserGreeting