import React, { Component } from 'react'

class Form extends Component{
    constructor()
    {
        super()
        this.state = {
            username : 'Enter name',
            topic : "dhoni"
        }
    }


    usernamehandler = (event) =>
    {
        this.setState({
            username : event.target.value
        })
    }

    topicchangehandler = (event) =>
    {
        this.setState({
            topic : event.target.value
        })
    }

    submithandler = () =>
    {
        alert(`${this.state.username}  and ${this.state.topic}`)
    }

    render()
    {
        return (
            <form onSubmit ={this.submithandler}>
                <div>
                    <label>UserName</label>
                    <input type = "text"  value={this.state.username} onChange = {this.usernamehandler}></input>
                </div>

                <div>
                    <label>Select one player</label>
                    <select value = {this.state.topic}  onChange ={this.topicchangehandler}>
                        <option value="dhoni">Dhoni</option>
                        <option value= "sachin">Sachin</option>
                        <option value = "kholi"> Kholi</option>
                    </select>
                </div>

                <button type="submit">Submit</button>
            </form>
        )
      }
    }


    export default Form