import  React, { Component } from 'react'
import ChildComponent from './ChildComponent'

class ParentCompoment extends Component{

   constructor()
   {
       super()
       this.state = {
           parentName : "Parent"
       }

       this.greeting = this.greeting.bind(this);

   }


   greeting(childname)
   {
       alert(`Im ${this.state.parentName} from ${childname}` );
   }
   
   
    render()
    {
        return (
        <div>

            <h1>Im parent</h1>
            <button onClick = {() => this.greeting(null)}>Click Me</button>
            <ChildComponent greethandler = {this.greeting}></ChildComponent>
        </div>
        )
    }

}

export default ParentCompoment